*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}
#root{
    height: 550px;
    width: 550px;
    
    margin: 0px auto;
}
nav{
    background-color: #21222A;
    display: flex;
    height: 91px;
    padding: 0px 26px;
    align-items:center;
    position: relative;
}
nav > img{
    height: 24.50119972229004px;
    width: 27.69435691833496px;
    margin-right: 6px;
    
}
nav > h3{
   
    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 22.2535px;
    
    letter-spacing: -0.05em;
    color: #61DAFB;
    margin-right: auto;
}
nav > h4{
    
    font-family: 'Inter';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    
    text-align: right;

    color: #DEEBF8;
}

main{
    width: 100%;
    height: 459px;
    background: #282D35;
    padding-top: 57px;
}
main > h1{
    width: 424px;
    height: 41px;
    

    font-family: 'Inter';
    font-style: normal;
    font-weight: 700;
    font-size: 39.06px;
    line-height: 47px;
    letter-spacing: -0.05em;
    color: #FFFFFF;
    margin-left: 27px;
}
main > ul{
    color:#61DAFB;
    margin-left: 51px;
    margin-top: 87px;
}
main > ul > li{
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 15px;
    color: #FFFFFF;
    
}
main > ul > li::marker{
    font-size: 1.4rem;
    color: #61DAFB;
}
